.introduction_text {
    padding: 10px 0 30px 0;
}

.question_types_title {
    font-weight: 600;
}

.lang {
    padding: 30px 0 30px 0;
    width: 270px;
    display: flex;  
    justify-content: space-between; 
}

.language {
    width: 130px;
    padding: 5px;
    cursor: pointer;
    transition: 0.3s;
}

.language:hover {
    background-color: #ffb842;
    transition: 0.3s;
}

.listen {
    width: 500px;
}